<template>
  <div class="register-page">
    <header>
      <div class="logo">
        <a href="/"></a>
      </div>
      <div class="login-guide">
        <span class="guide"></span>
        <router-link :to="{ name: 'Login' }">
          <el-button type="warning"></el-button>
        </router-link>
      </div>
    </header>
    <article>
      <div class="register-form">
        <el-form
          :model="registerForm"
          status-icon
          :rules="rules"
          ref="registerForm"
          label-width="100px"
        >
          <el-form-item label="" prop="userName">
            <el-input
              type="text"
              v-model="registerForm.userName"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input type="password" v-model="registerForm.password" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item class="pw-strength" label="">
            <div class="bar" :class="strengthClass"></div>
            <div class="letter">
              <span></span>
              <span></span>
              <span></span>
            </div>
          </el-form-item>
          <el-form-item label="" prop="rePassword">
            <el-input v-model="registerForm.rePassword" type="password"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('registerForm')"
              ></el-button>
          </el-form-item>
          <el-form-item class="user-agreement-content">
            <el-checkbox v-model="checked">
              
              <a href="#"></a>
              <a href="#"></a>
               
            </el-checkbox>
          </el-form-item>
        </el-form>
      </div>
    </article>
    <footer>
      <p class="copyright">
        <router-link to="/">meituan.com</router-link>
        <router-link to="/">ICP070791</router-link>
        11010502025545
      </p>
    </footer>
  </div>
</template>

<script>
/* eslint prefer-template: off */
import api from '@/api';

export default {
  data() {
    // 
    const phoneReg = /^(13|14|15|16|17|18)\d{9}$/g;
    const validateUserName = (rule, value, callback) => {
      if (!value) {
        callback(new Error(''));
      } else if (!value.match(phoneReg)) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    // 
    const passwordReg1 = /^(?![0-9]+$)(?![a-zA-Z]+$)(?![_]+$)[0-9A-Za-z_]{6,16}$/gi;
    const validatePassword = (rule, value, callback) => {
      if (!value) {
        callback(new Error(''));
      } else if (!value.match(passwordReg1)) {
        callback(new Error('6-16'));
      } else {
        if (value.length >= 6 && value.length <= 10) {
          this.strengthClass = 'weak';
        } else if (value.length >= 10 && value.length <= 13) {
          this.strengthClass = 'normal';
        } else {
          this.strengthClass = 'strong';
        }
        callback();
      }
    };
    // 
    const validateRePassword = (rule, value, callback) => {
      if (!value) {
        callback(new Error(''));
      } else if (value !== this.registerForm.password) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    return {
      registerForm: {
        userName: '',
        password: '',
        rePassword: '',
      },
      strengthClass: '',
      checked: false,
      rules: {
        userName: [
          { validator: validateUserName, trigger: 'blur' },
        ],
        password: [
          { validator: validatePassword, trigger: 'blur' },
        ],
        rePassword: [
          { validator: validateRePassword, trigger: 'blur' },
        ],
      },
    };
  },
  methods: {
    submitForm(formName) {
      console.log(formName);
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          const resp = await api.register(this.registerForm);
          console.log(resp);
          if (resp.status === 'success') {
            this.$message('' + resp.msg);
            this.$router.push({ name: 'Login' });
          } else {
            this.$message('' + resp.msg);
          }
        }
      });
    },
  },
};
</script>

<style lang="scss">
@import "~@/assets/css/register";
</style>
